<template>
  <view class="activity-item" @click="onItemClick">
    <view class="tag-wrapper">
      <view class="tag">{{ item.categoryName }}</view>
    </view>
    <view class="user-info">
      <image class="avatar" :src="item.avatar" mode="aspectFill" />
      <view class="info">
        <text class="name">{{ item.userName }}</text>
        <text class="time">{{ item.createTime }}</text>
      </view>
      <view v-if="item.status" class="status">{{ item.status }}</view>
    </view>
    <view class="content">{{ item.content }}</view>
    <view class="image-list" v-if="item.images && item.images.length">
      <image
        v-for="(img, index) in item.images"
        :key="index"
        :src="img"
        mode="aspectFill"
        class="image-item"
        @click.stop="previewImage(index)"
      />
    </view>
    <view class="location" v-if="item.location">
      <text class="icon">üìç</text>
      <text class="text">{{ item.location }}</text>
    </view>
    <view class="footer">
      <view class="action-item" @click.stop="handleComment">
        <!-- <image class="icon" src="/static/images/comment.png" mode="aspectFit" /> -->
        <text class="text">Êü•ÁúãËØÑ‰ª∑</text>
      </view>
      <view class="action-item" @click.stop="handleLike">
        <image class="icon" src="/static/images/like.png" mode="aspectFit" />
        <text class="text">{{ item.likes || 0 }}</text>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  name: 'ActivityItem',
  props: {
    item: {
      type: Object,
      default: () => ({}),
    },
  },
  methods: {
    onItemClick() {
      this.$emit('click', this.item)
    },
    previewImage(current) {
      uni.previewImage({
        urls: this.item.images,
        current,
      })
    },
    handleComment() {
      this.$emit('comment', this.item)
    },
    handleLike() {
      this.$emit('like', this.item)
    },
  },
}
</script>

